.app-form {
  margin-top: 30px;
}

.app-form:last-child {
  margin-bottom: 0;
}

.app-form__message {
  font-size: var(--app-font-size-small);
  line-height: var(--app-line-height-small);
  color: var(--app-alert-active);
  text-align: center;
  margin: 0;
  margin-top: var(--app-space-large);
  white-space: pre-line;
}