.app-classic-button {
  width: 100%;
  height: 50px;
  color: var(--app-light-active);
  background-color: var(--app-dark-active);
  padding: 5px 10px;
  cursor: pointer;
  transition: background-color .3s, color .3s;
}

.app-classic-button_filled {
  border: 1px solid var(--app-dark-active);
}

.app-classic-button_filled:hover {
  background-color: var(--app-dark-hover);
}

.app-classic-button_outlined {
  border: 1px solid var(--app-light-active);
}

.app-classic-button_outlined:hover {
  background-color: var(--app-light-active);
  color: var(--app-dark-active);
}

.app-classic-button_outlined:disabled {
  color: var(--app-light-disabled);
}

.app-classic-button__label {
  font-size: var( --app-font-size-large);
  line-height: var(--app-line-height-large);
}

.app-classic-button:disabled {
  border: 1px solid transparent;
  background-color: var(--app-dark-disabled);
  cursor: auto;
}